@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.header-image {
    width:100%;
}
#link {
    color:rgb(0, 0, 0) !important;
    font-weight: 650;
    font-size: 19px;
}

.navbar-toggler {
    background-color: black;
}
.candidatureDiv {
    margin: 0% 20%;
    border-radius: 20px;
    background-color: rgb(148, 147, 147);
    font-size: 16px;
    font-weight: 700;
    font-family: 'Helvetica', sans-serif;
}

.candidatureForm {
    padding: 10% 10%;
    display: block;
    position: relative;
}

.formGroup {
    margin:10% 10%;
}

.inputText {
    width:100%;
    border-radius: 25px;
    font-size: 16px;
    font-weight: 700;
    display: block;
    position: relative;
    font-weight: bold;
    z-index: 2;
    background: none;
    border:none;
    border-bottom: 1px solid white;
    font-family: 'Helvetica', sans-serif;
}

.labelText {
    display: block;
    top:-30px;
    font-family: 'Helvetica', sans-serif;
    left:10px;
    position: relative;
    align-content: center;
    color:black;
    font-family: 'Helvetica', sans-serif;
    font-size: 16px;
    z-index: 1;
    transition: all 0.3s ease-out;
}

.inputText:focus + .labelText, .inputText:valid + .labelText{
    top: -45px;
    font-size: 11px;
    color: white;
}

.inputText:focus, .inputText:valid {
    outline: none;
    padding: 0px 10px;
}

input, label {
    display: block;
    position: relative;
}
button {
    border-radius: 30px;
}

i {
    font-family: 'Material Icons', serif;
    font-style: normal;
    font-size: 1.5em;
    display: inline-block;
    vertical-align: middle;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-feature-settings: 'liga';
            font-feature-settings: 'liga';
    padding: 1em;
    color:#777;
}

.customNavbar {
    border-bottom: 1px solid #eee;
    background: #fff;
    font-family: 'Open Sans', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
    font-size: 1em;
    color: #1a8fff;
    position: relative;
}
Button {
    outline: none;
}
.divPerso {
    width: 100%;
    height: 200px;
    text-align: center;
    color:white;
    border-radius: 30px;
    padding: 0 10%;
}

.btn {
    border: none;
    margin-right: 2em;
    border-radius: 0.6em;
    padding: 1em 1em;
}

.btn:focus {
    color:black;
}

.text-center {
    margin-top: 3em;
}
.card-header, #collapseOne {
    cursor: pointer;
}
